/* @import url("https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;700&display=swap"); */

.tilted-card-figure {
  position: relative;
  width: 100%;
  height: 100%;
  perspective: 800px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.tilted-card-mobile-alert {
  position: absolute;
  top: 1rem;
  text-align: center;
  font-size: 0.875rem;
  display: none;
}

@media (max-width: 640px) {
  .tilted-card-mobile-alert {
    display: block;
  }
  .tilted-card-caption {
    display: none;
  }
}

.tilted-card-inner {
  position: relative;
  transform-style: preserve-3d;
}

.tilted-card-img {
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
  border-radius: 15px;
  will-change: transform;
  transform: translateZ(0);
}

/* 텍스트도 기울어짐에 포함되며, 이미지 아래쪽 가운데 정렬 */
.tilted-card-overlay {
  position: absolute;
  top: 310px;           /* 이미지 높이가 300px 이므로, 이미지 바로 아래 */
  left: 50%;            /* 가로 중앙 */
  z-index: 2;
  will-change: transform;
  transform: translate(-50%, 0) translateZ(20px);
}

.tilted-card-caption {
  pointer-events: none;
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 4px;
  background-color: #fff;
  padding: 4px 10px;
  font-size: 10px;
  color: #2d2d2d;
  opacity: 0.5; /* 50% 투명도 */
  z-index: 3;
}

.tilted-card-demo-text {
  /* 인라인 블록으로 지정 (텍스트 길이에 맞춰 늘어남) */
  display: inline-block;

  text-transform: capitalize;
  color: #fff;
  letter-spacing: -0.5px;
  background: #0006;
  border-radius: 30px;

  /* 외부 여백(필요시 조정) */
  margin: 0;

  /* 내부 여백 (텍스트와 박스 간격) */
  padding: 0.5rem 1rem;

  font-weight: 900;
  box-shadow: 0 5px 30px #06060659;
  /* 만약 글자 길이가 길 때 줄바꿈 없이 한 줄로만 보이길 원하면 ↓ */
  white-space: nowrap;
}
